import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { Textarea } from '../../../../common/formControls/formControls';
import { MaxLength, required } from '../../../../utilits/validators';
import s from './new_post.module.css'

let maxLength20 = MaxLength(20);

let NewPost = (props) => {
    let newText = React.createRef();

    const addPost = (formData) => {
        props.addNewPost(formData.text);
        
        
        // if(formData.text){
        // }
        // if (formData.text === undefined){
        //     let undef =''
        //     props.addNewPost(undef);
        // }
    };

    return (
        <div className={s.wrapper}>
            <ReduxPostTextForm onSubmit={addPost} />
        </div>
    )
}

const PostTextForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={s.container}>
            <div className={s.postTop}>
                <a href='#'><img src="https://semantic-ui.com/images/avatar2/large/matthew.png" className={s.ava} /></a>

                <Field component={Textarea} name='text' placeholder='   ?' className={s.text}  validate={[required,maxLength20]}/>

                <a href='#' ><img src="https://www.svgrepo.com/show/494000/smile.svg" className={s.smile} /></a>
            </div >
            <div className={s.postBottom}>
                <a href="#">
                    <img src="https://www.svgrepo.com/show/506315/settings.svg" alt="" className={s.set} />
                </a>

                <label for="file-upload" className={s.upload}>
                    <img src="https://www.svgrepo.com/show/502794/photo-camera.svg" alt="Upload Image" className={s.img} />
                </label>
                <input id="file-upload" type="file" accept="image/*" className={s.input} />

                <button  className={s.public}></button>
            </div >
        </form >
    )
}
const ReduxPostTextForm = reduxForm({
    form: 'textpost'
})(PostTextForm)
export default NewPost