import React from 'react';
import Preloader from '../../../../common/preloader/preloader';
import s from './picture.module.css'

let Picture = (props) =>{
    if(!props.profile) {
        return Preloader
    }

    return(
        <div className={s.container}>
            <div className={s.img}>
                <img src={props.profile.photos.small} className={s.ava} />
                {/* <img src="https://semantic-ui.com/images/avatar2/large/matthew.png" className={s.ava} /> */}
            </div>
            <div className={s.circle}>
                <div className={s.online}></div>
            </div>
        </div>
        
    )
}

export default Picture